import "./styles.css";
import menu from "./sample.json";
import * as React from "react";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li key={keys[i]}>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul key={index}>{items}</ul>;
}

function allMenus() {
  const items = [];
  for (let i = 0; i < menu.length; i++) {
    items.push(menus(i));
  }
  return <>{items}</>;
}

export default function App() {
  const [selectMenu, setSelectMenu] = React.useState(1);

  const handleChange = (event) => {
    setSelectMenu(event.target.value);
  };
  return (
    <>
      <h3>選択</h3>
      <Box sx={{ minWidth: 120, pb: 2 }}>
        <FormControl fullWidth>
          <InputLabel id="demo-simple-select-label">メニュー</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={selectMenu}
            label="メニュー"
            onChange={handleChange}
          >
            {menu.map((item) => {
              return (
                <MenuItem value={item.index} key={item.index}>
                  {item.menu}
                </MenuItem>
              );
            })}
          </Select>
        </FormControl>
        <p>{menu[selectMenu - 1].price}円</p>
      </Box>
      <h3>テーブル</h3>
      <table>
        <tbody>
          <tr>
            {Object.keys(menu[0]).map((item) => {
              return <th>{item}</th>;
            })}
          </tr>
          {menu.map((item) => {
            return (
              <tr key={item.index}>
                <td>{item.index}</td>
                <td>{item.menu}</td>
                <td>{item.price}円</td>
                <td>
                  {JSON.parse(item.image.toLowerCase()) === true
                    ? "あり"
                    : "なし"}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <h3>リスト</h3>
      {allMenus()}
    </>
  );
}
